html,
body {
  margin: 0;
  padding: 0;
  background-color: #13294b;
}
#root {
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.main {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
}

.title {
  font-size: 2rem;
  color: #e7e0c9;
}

.buttonWrap {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  min-width: 45%;
}
.button {
  border: #e7e0c9;
  background-color: #e7e0c9;
  border-radius: 25px;
  box-shadow: 2px 2px 2px #6b7aa1;
  min-width: 20%;
  height: 2rem;
  cursor: pointer;
}
